import React, { useState, useEffect } from "react";
import backendURL from "../api";
import "./Orders.scss";

const Orders = () => {
	const [orders, setOrders] = useState([]);

	useEffect(() => {
		getOrders();
	}, []);

	const getOrders = async () => {
		const res = await fetch(
			`${backendURL}/api/v1/orders/getAllOrders`,
			// `http://localhost:3000/api/v1/orders/getAllOrders`,
			{
				method: "GET",
				headers: {
					"Content-Type": "application/json",
					Authorization: `Bearer ${localStorage.getItem("jwt")}`,
				},
			}
		);

		const data = await res.json();

		if (data.status === "success") {
			setOrders(data.data.orders);
		}
	};

	const getDate = (date) => {
		const newDate = new Date(date);
		const day = newDate.getDate();
		const month = newDate.getMonth() + 1;
		const year = newDate.getFullYear();
		const fullDate = `${day}/${month}/${year}`;
		return fullDate;
	};

	return (
		<div className="order-container">
			<h1>Orders</h1>
			<div className="order-buttons"></div>
			<table>
				<thead>
					<tr>
						<th>Name</th>
						<th>Status</th>
						<th>Address</th>
						<th>Items</th>
						<th>Date</th>
						<th>Subtotal</th>
						<th>Shipping</th>
						<th>Total</th>
					</tr>
				</thead>
				<tbody>
					{orders &&
						orders.map((order) => (
							<tr key={order.orderId}>
								<td>{order.contact.address.name}</td>
								<td>{order.orderStatus}</td>
								<td>{`${order.contact.address.address}, ${order.contact.address.city}, ${order.contact.address.state}, ${order.contact.address.country}`}</td>
								<td>
									{order.items.map((item) => {
										return (
											<div>
												<p>{item.name}</p>
												<p>{item.quantity}</p>
											</div>
										);
									})}
								</td>
								<td>{getDate(order.date)}</td>
								<td>{order.subtotal}</td>
								<td>{order.deliveryFee}</td>
								<td>{order.total}</td>
							</tr>
						))}
				</tbody>
			</table>
		</div>
	);
};

export default Orders;
