import React, { useState, useEffect } from "react";
import backendURL from "../api";
import "./Transactions.scss";

const Transactions = () => {
	const [transactions, setTransactions] = useState([]);

	useEffect(() => {
		getTransactions();
	}, []);

	const getTransactions = async () => {
		const res = await fetch(`${backendURL}/api/v1/payment/`, {
			method: "GET",
			headers: {
				"Content-Type": "application/json",
				Authorization: `Bearer ${localStorage.getItem("jwt")}`,
			},
		});

		const data = await res.json();

		if (data.status === "success") {
			setTransactions(data.data.transactions);
		}
	};

	const getDate = (date) => {
		const newDate = new Date(date);
		const day = newDate.getDate();
		const month = newDate.getMonth() + 1;
		const year = newDate.getFullYear();
		const fullDate = `${day}/${month}/${year}`;
		return fullDate;
	};

	return (
		<div className="transaction-container">
			<h1>Transactions</h1>
			<div className="transaction-buttons"></div>
			<table>
				<thead>
					<tr>
						<th>Name</th>
						<th>Email</th>
						<th>Amount</th>
						<th>Status</th>
						<th>Date</th>
					</tr>
				</thead>
				<tbody>
					{transactions.map((transaction) => (
						<tr key={transaction._id}>
							<td>{transaction.orderId.contact.address.name}</td>
							<td>{transaction.orderId.contact.address.email}</td>
							<td>{transaction.amount}</td>
							<td>{transaction.paymentStatus}</td>
							<td>{getDate(transaction.createdAt)}</td>
						</tr>
					))}
				</tbody>
			</table>
		</div>
	);
};

export default Transactions;
