import React, { useState } from "react";
import Modal from "react-modal";
import backendURL from "../../api";
import "./ProductsModal.scss";

const AddProductsModal = ({ isOpen, onClose }) => {
	const [productData, setProductData] = useState({
		name: "",
		price: 0,
		image: null,
		summary: "",
		description: "",
		quantity: 0,
		category: "",
	});

	const addProduct = async (productData) => {
		console.log(productData);
		const response = await fetch(`${backendURL}/api/v1/wine/`, {
			method: "POST",
			headers: {
				"Content-Type": "application/json",
				Authorization: `Bearer ${localStorage.getItem("jwt")}`,
			},
			body: JSON.stringify(productData),
		});
		const newProduct = await response.json();

		if (newProduct.status === "success") {
			setProductData(newProduct.data.wineProduct);

			onClose();
		}
	};

	const handleInputChange = (e) => {
		setProductData({ ...productData, [e.target.name]: e.target.value });
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		addProduct(productData);
		onClose();
	};

	return (
		<Modal
			isOpen={isOpen}
			onRequestClose={onClose}
			style={{
				overlay: {
					position: "fixed",
					top: 0,
					backgroundColor: "rgba(0, 0, 0, 0.5)",
					zIndex: 1000,
				},
				content: {
					top: "50%",
					left: "50%",
					right: "auto",
					bottom: "auto",
					marginRight: "-50%",
					transform: "translate(-50%, -50%)",
					background: "transparent",
					border: "none",
					zIndex: 5,
					padding: 0,
				},
			}}
		>
			<div className="modal-content">
				<div className="modal-header">
					<h5>Add Product</h5>
				</div>
				<div className="modal-body">
					<form onSubmit={handleSubmit}>
						<div>
							<label htmlFor="name">
								Name
								<input
									type="text"
									name="name"
									id="name"
									value={productData.name}
									onChange={handleInputChange}
								/>
							</label>
						</div>

						<div>
							<label htmlFor="price">
								Price
								<input
									type="number"
									name="price"
									id="price"
									value={productData.price}
									onChange={handleInputChange}
								/>
							</label>

							<div className="upload-container">
								<label htmlFor="image">Upload Image:</label>
								<input
									type="file"
									id="image"
									name="image"
									value={productData.image}
									onChange={handleInputChange}
									required
								/>
								<label htmlFor="image" className="upload-button">
									Choose Image
								</label>
							</div>
						</div>

						<div>
							<label htmlFor="summary">
								Summary
								<textarea
									name="summary"
									id="summary"
									rows="3"
									value={productData.summary}
									onChange={handleInputChange}
								></textarea>
							</label>

							<label htmlFor="description">
								Description
								<textarea
									name="description"
									id="description"
									rows="6"
									value={productData.description}
									onChange={handleInputChange}
								></textarea>
							</label>
						</div>

						<div>
							<label htmlFor="quantity">
								Quantity
								<input
									type="number"
									name="quantity"
									id="quantity"
									value={productData.quantity}
									onChange={handleInputChange}
								/>
							</label>

							<label htmlFor="category">
								Category
								<input
									type="text"
									name="category"
									id="category"
									value={productData.category}
									onChange={handleInputChange}
								/>
							</label>
						</div>

						<div className="modal-buttons">
							<button type="submit" className="add-button">
								Add Product
							</button>
							<button type="button" className="cancel-button" onClick={onClose}>
								Cancel
							</button>
						</div>
					</form>
				</div>
			</div>
		</Modal>
	);
};

export default AddProductsModal;
